//React Components

//Chakra UI
import {
  // useDisclosure,
  // Collapse,
  Box,
  Text,
  Icon,
  UnorderedList,
  ListItem
} from "@chakra-ui/react";

import { FaTag } from "react-icons/fa";

const CollapseCard = ({ description, techies }) => {

  // const { isOpen, onToggle } = useDisclosure()

  return (
    <>
      {/* <Collapse in={isOpen} startingHeight={50}  animateOpacity> */}
      <Box
        my={1}
        pb={2}
        fontWeight="semibold"
        as="h4"
        lineHeight="tight"
        borderBottom="1px"
      >
        {
          description.map(p =>
            <Box mb={4}>
              {
                p.split('\n').map(
                  str => <Text mb={1}> {str} </Text>
                )
              }
            </Box>
          )
        }

      </Box>
      <Box>
        <Text
          fontWeight="semibold"
          fontSize="lg"
          mb={1}
        >
          <Icon as={FaTag} me={1} />
          Technologies
        </Text>
        <UnorderedList pb={2}>
          {
            techies.map((tech, i) => {
              return (
                <ListItem key={i} ms={1} m>
                  {tech}
                </ListItem>
              )
            })
          }
        </UnorderedList>
      </Box>
      {/* 
      </Collapse>
      <Button onClick={onToggle}>Show More</Button> */}
    </>
  )
}


export default CollapseCard